:host {
  --pfe-cta--Padding:      .6rem 0;
  --pfe-cta--BorderRadius: 0;
  --pfe-cta__arrow--Display:     inline;
  --pfe-cta__arrow--Padding:     0 .125rem 0 .375rem;
  --pfe-cta__arrow--MarginLeft:  calc(var(--pfe-theme--content-spacer, 24px) * 0.25);
  --pfe-cta--BackgroundColor:         transparent;
  --pfe-cta--BackgroundColor--hover:  transparent;
  --pfe-cta--BackgroundColor--focus:  transparent;
  --pfe-cta--BorderColor:           transparent;
  --pfe-cta--BorderColor--hover:    transparent;
  --pfe-cta--BorderColor--focus:    transparent;
  --pfe-cta__inner--BorderColor:        transparent;
  --pfe-cta__inner--BorderColor--focus: transparent;
  --pfe-cta--Color:          var(--pfe-broadcasted--ui-link, #06c);
  --pfe-cta--Color--hover:   var(--pfe-broadcasted--ui-link--hover, #003366);
  --pfe-cta--Color--focus:   var(--pfe-broadcasted--ui-link--focus, #003366);
  --pfe-cta--TextDecoration:          none;
  --pfe-cta--TextDecoration--hover:   none;
  --pfe-cta--TextDecoration--focus:   none;
  --pfe-cta--FontWeight:              var(--pfe-theme--font-weight--bold, 700);
  display: inline-block;
  position: relative;
  vertical-align: middle;
  transition: padding var(--pfe-theme--animation-speed, 0.3s) var(--pfe-theme--animation-timing, cubic-bezier(0.465, 0.183, 0.153, 0.946));
  background-color: var(--pfe-cta--BackgroundColor);
  border-color: var(--pfe-cta--BorderColor);
  background-color: var(--pfe-cta--BackgroundColor);
  border-radius: var(--pfe-cta--BorderRadius);
  border: var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-cta--BorderColor);
  padding: var(--pfe-cta--Padding);
}

:host ::slotted(button),
:host ::slotted(input) {
  background-color: transparent;
  border: none;
  margin: 0;
  padding: 0;
}

:host ::slotted(*) {
  display: inline;
  color: var(--pfe-cta--Color, #003366) !important;
  font-family: var(--pfe-cta--FontFamily, var(--pfe-theme--font-family--heading, "Overpass", Overpass, Helvetica, helvetica, arial, sans-serif));
  font-size: var(--pfe-cta--FontSize, var(--pfe-theme--font-size, 16px));
  font-weight: var(--pfe-cta--FontWeight);
  line-height: var(--pfe-cta--LineHeight, var(--pfe-theme--line-height, 1.5));
  text-decoration: var(--pfe-cta--TextDecoration) !important;
}

:host ::slotted(*)::before {
  content: "";
  display: block;
  box-sizing: border-box;
  position: absolute;
  top: 2px;
  left: 2px;
  bottom: 2px;
  right: 2px;
  border: var(--pfe-theme--surface--border-width, 1px) var(--pfe-theme--surface--border-style, solid) var(--pfe-cta__inner--BorderColor);
  border-radius: 2px;
  outline: none;
}

:host ::slotted(*:not(:disabled)) {
  cursor: pointer;
}

:host .pfe-cta--arrow {
  display: var(--pfe-cta__arrow--arrow--Display);
  padding: var(--pfe-cta__arrow--Padding);
  fill: var(--pfe-cta--Color);
  position: relative;
  top: .1rem;
  width: 13px;
  height: 13px;
  transition: padding var(--pfe-theme--animation-speed, 0.3s) var(--pfe-theme--animation-timing, cubic-bezier(0.465, 0.183, 0.153, 0.946));
}

:host ::slotted(:hover) {
  color: var(--pfe-cta--Color--hover) !important;
  text-decoration: var(--pfe-cta--TextDecoration--hover) !important;
}

:host ::slotted(:focus) {
  outline: none !important;
  color: var(--pfe-cta--Color--focus) !important;
  text-decoration: var(--pfe-cta--TextDecoration--focus) !important;
}

:host ::slotted(:focus)::before {
  border: 1px solid var(--pfe-cta__inner--BorderColor--focus);
}

:host ::slotted(:focus) .pfe-cta--arrow {
  fill: var(--pfe-cta--Color--focus);
}

:host(::after) {
  margin-left: var(--pfe-cta__arrow--MarginLeft);
}

:host(:hover) {
  background-color: var(--pfe-cta--BackgroundColor--hover);
  border-color: var(--pfe-cta--BorderColor--hover);
}

:host(:hover) ::slotted(*) {
  color: var(--pfe-cta--Color--hover) !important;
  text-decoration: var(--pfe-cta--TextDecoration--hover) !important;
}

:host(:hover) ::slotted(*)::before {
  border: var(--pfe-theme--surface--border-width, 1px) var(--pfe-theme--surface--border-style, solid) var(--pfe-cta__inner--BorderColor--hover);
}

:host(:hover) .pfe-cta--arrow {
  fill: var(--pfe-cta--Color--hover);
}

:host(.focus-within) {
  outline: none !important;
  background-color: var(--pfe-cta--BackgroundColor--focus);
  border-color: var(--pfe-cta--BorderColor--focus);
}

:host(.focus-within) ::slotted(*) {
  color: var(--pfe-cta--Color--focus) !important;
  text-decoration: var(--pfe-cta--TextDecoration--focus) !important;
}

:host(.focus-within) .pfe-cta--arrow {
  fill: var(--pfe-cta--Color--focus);
}

:host([pfe-priority]) {
  --pfe-cta--Padding: var(--pfe-theme--container-padding, 16px) calc(var(--pfe-theme--container-padding, 16px) * 2);
}

:host([pfe-priority]) .pfe-cta--wrapper {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

:host(:not([pfe-priority])) {
  --pfe-cta--Color: var(--pfe-broadcasted--color--ui-link, var(--pfe-theme--color--ui-link, #06c));
  --pfe-cta--inner--BorderColor--hover: var(--pfe-broadcasted--color--ui-link--hover, var(--pfe-theme--color--ui-link--hover, #003366));
  --pfe-cta--Color--hover: var(--pfe-broadcasted--color--ui-link--hover, var(--pfe-theme--color--ui-link--hover, #003366));
  --pfe-cta--BackgroundColor--focus: rgba(40, 151, 240, 0.2);
  --pfe-cta--Color--focus: var(--pfe-broadcasted--color--ui-link--focus, var(--pfe-theme--color--ui-link--focus, #003366));
}

:host([pfe-priority="secondary"]) {
  --pfe-cta--BorderRadius:              var(--pfe-theme--ui--border-radius, 2px);
  --pfe-cta__arrow--Display:            none;
  --pfe-cta--BackgroundColor:           transparent;
  --pfe-cta--BorderColor:               var(--pfe-broadcasted--color--text, var(--pfe-theme--color--ui-complement, #464646));
  --pfe-cta__inner--BorderColor:        transparent;
  --pfe-cta--Color:                     var(--pfe-broadcasted--color--text, var(--pfe-theme--color--ui-complement, #464646));
  --pfe-cta--BackgroundColor--hover:    var(--pfe-theme--color--ui-complement, #464646);
  --pfe-cta--BorderColor--hover:        var(--pfe-broadcasted--color--text, var(--pfe-theme--color--ui-complement, #464646));
  --pfe-cta--Color--hover:              var(--pfe-theme--color--ui-complement--text, #fff);
  --pfe-cta--BackgroundColor--focus:    var(--pfe-theme--color--surface--lighter, #ececec);
  --pfe-cta--BorderColor--focus:        var(--pfe-theme--color--ui-complement, #464646);
  --pfe-cta__inner--BorderColor--focus: var(--pfe-broadcasted--color--text, var(--pfe-theme--color--ui-complement, #464646));
  --pfe-cta--Color--focus:              var(--pfe-theme--color--ui-complement, #464646);
}

:host([pfe-priority="primary"]) {
  --pfe-cta--BorderRadius:              var(--pfe-theme--ui--border-radius, 2px);
  --pfe-cta__arrow--Display:            none;
  --pfe-cta--BackgroundColor:           var(--pfe-theme--color--ui-accent, #fe460d);
  --pfe-cta--BorderColor:               var(--pfe-theme--color--ui-accent, #fe460d);
  --pfe-cta__inner--BorderColor:        transparent;
  --pfe-cta--Color:                     var(--pfe-theme--color--ui-accent--text, #fff);
  --pfe-cta--BackgroundColor--hover:    var(--pfe-theme--color--ui-accent--hover, #a42701);
  --pfe-cta--BorderColor--hover:        var(--pfe-theme--color--ui-accent--hover, #a42701);
  --pfe-cta--Color--hover:              var(--pfe-theme--color--ui-accent--text, #fff);
  --pfe-cta--BackgroundColor--focus:    var(--pfe-theme--color--ui-accent, #fe460d);
  --pfe-cta--BorderColor--focus:        var(--pfe-theme--color--ui-accent, #fe460d);
  --pfe-cta__inner--BorderColor--focus: var(--pfe-theme--color--ui-accent--text, #fff);
  --pfe-cta--Color--focus:              var(--pfe-theme--color--ui-accent--text, #fff);
}

:host([pfe-priority="primary"][pfe-color="lightest"]) {
  --pfe-cta--BackgroundColor:           var(--pfe-theme--color--surface--lightest, #fff);
  --pfe-cta--BorderColor:               var(--pfe-theme--color--surface--lightest, #fff);
  --pfe-cta__inner--BorderColor:        transparent;
  --pfe-cta--Color:                     var(--pfe-theme--color--surface--lightest--text, #333);
  --pfe-cta--BackgroundColor--hover:    var(--pfe-theme--color--surface--lighter, #ececec);
  --pfe-cta--BorderColor--hover:        var(--pfe-theme--color--surface--lighter, #ececec);
  --pfe-cta--Color--hover:              var(--pfe-theme--color--surface--lightest--text, #333);
  --pfe-cta--BackgroundColor--focus:    var(--pfe-theme--color--surface--lightest, #fff);
  --pfe-cta--BorderColor--focus:        var(--pfe-theme--color--surface--lightest, #fff);
  --pfe-cta__inner--BorderColor--focus: var(--pfe-theme--color--surface--darkest, #131313);
  --pfe-cta--Color--focus:              var(--pfe-theme--color--surface--lightest--text, #333);
}

:host(:not([pfe-priority])[pfe-color="accent"]) {
  --pfe-cta--Color: var(--pfe-theme--color--ui-accent, #fe460d);
}

:host([pfe-priority="secondary"][pfe-color="accent"]) {
  --pfe-cta--BackgroundColor:           transparent;
  --pfe-cta--BorderColor:               var(--pfe-theme--color--ui-accent, #fe460d);
  --pfe-cta--BackgroundColor--hover:    var(--pfe-theme--color--ui-accent, #fe460d);
  --pfe-cta__inner--BorderColor:        transparent;
  --pfe-cta--Color:                     var(--pfe-theme--color--ui-accent, #fe460d);
  --pfe-cta--BackgroundColor--hover:    var(--pfe-theme--color--ui-accent, #fe460d);
  --pfe-cta--BorderColor--hover:        var(--pfe-theme--color--ui-accent, #fe460d);
  --pfe-cta--Color--hover:              var(--pfe-theme--color--ui-accent--text, #fff);
  --pfe-cta--BackgroundColor--focus:    var(--pfe-theme--color--surface--lighter, #ececec);
  --pfe-cta--BorderColor--focus:        var(--pfe-theme--color--ui-accent, #fe460d);
  --pfe-cta__inner--BorderColor--focus: var(--pfe-theme--color--ui-accent, #fe460d);
  --pfe-cta--Color--focus:              var(--pfe-theme--color--ui-accent, #fe460d);
}

:host([pfe-priority="secondary"][pfe-color="lightest"]) {
  --pfe-cta--BackgroundColor:           transparent;
  --pfe-cta--BorderColor:               var(--pfe-theme--color--surface--lightest, #fff);
  --pfe-cta__inner--BorderColor:        transparent;
  --pfe-cta--Color:                     var(--pfe-theme--color--surface--darkest--text, #fff);
  --pfe-cta--BackgroundColor--hover:    var(--pfe-theme--color--surface--lighter, #ececec);
  --pfe-cta--BorderColor--hover:        var(--pfe-theme--color--surface--lighter, #ececec);
  --pfe-cta--Color--hover:              var(--pfe-theme--color--surface--lightest--text, #333);
  --pfe-cta--BackgroundColor--focus:    var(--pfe-theme--color--surface--lightest, #fff);
  --pfe-cta--BorderColor--focus:        var(--pfe-theme--color--surface--lightest, #fff);
  --pfe-cta__inner--BorderColor--focus: var(--pfe-theme--color--surface--darkest, #131313);
  --pfe-cta--Color--focus:              var(--pfe-theme--color--surface--lightest--text, #333);
}

:host([pfe-priority="secondary"][pfe-color="accent"].focus-within) {
  --pfe-cta--Color--hover: var(--pfe-theme--color--ui-accent, #fe460d);
}

:host([pfe-priority="primary"][pfe-color="base"]) {
  --pfe-cta--BackgroundColor:           var(--pfe-theme--color--ui-base, #0477a4);
  --pfe-cta--BorderColor:               var(--pfe-theme--color--ui-base, #0477a4);
  --pfe-cta__inner--BorderColor:        transparent;
  --pfe-cta--Color:                     var(--pfe-theme--color--ui-base--text, #fff);
  --pfe-cta--BackgroundColor--hover:    var(--pfe-theme--color--ui-base--hover, #022f40);
  --pfe-cta--BorderColor--hover:        var(--pfe-theme--color--ui-base--hover, #022f40);
  --pfe-cta--Color--hover:              var(--pfe-theme--color--ui-base--text, #fff);
  --pfe-cta--BackgroundColor--focus:    var(--pfe-theme--color--ui-base, #0477a4);
  --pfe-cta__inner--BorderColor--focus: var(--pfe-theme--color--ui-base--text, #fff);
  --pfe-cta--BorderColor--focus:        var(--pfe-theme--color--surface--darker, #464646);
  --pfe-cta--Color--focus:              var(--pfe-theme--color--ui-base--text, #fff);
}

:host([pfe-priority="secondary"][pfe-color="base"]) {
  --pfe-cta--BackgroundColor:           transparent;
  --pfe-cta--BorderColor:               var(--pfe-theme--color--ui-base, #0477a4);
  --pfe-cta__inner--BorderColor:        transparent;
  --pfe-cta--Color:                     var(--pfe-theme--color--ui-base, #0477a4);
  --pfe-cta--BackgroundColor--hover:    var(--pfe-theme--color--ui-base, #0477a4);
  --pfe-cta--BorderColor--hover:        var(--pfe-theme--color--ui-base, #0477a4);
  --pfe-cta--Color--hover:              var(--pfe-theme--color--ui-base--text, #fff);
  --pfe-cta--BackgroundColor--focus:    rgba(40, 151, 240, 0.2);
  --pfe-cta__inner--BorderColor--focus: var(--pfe-theme--color--ui-base, #0477a4);
  --pfe-cta--BorderColor--focus:        var(--pfe-theme--color--ui-base, #0477a4);
  --pfe-cta--Color--focus:              var(--pfe-theme--color--ui-base, #0477a4);
}

:host([pfe-priority="secondary"][pfe-color="base"].focus-within) {
  --pfe-cta--Color--hover:            var(--pfe-theme--color--ui-base, #0477a4);
}

:host(:not([pfe-priority])[pfe-color="complement"]) {
  --pfe-cta--Color:        var(--pfe-theme--color--ui-complement, #464646);
  --pfe-cta--Color--hover: var(--pfe-theme--color--ui-complement--hover, #131313);
}

:host([pfe-priority="primary"][pfe-color="complement"]) {
  --pfe-cta--BackgroundColor:           var(--pfe-theme--color--ui-complement, #464646);
  --pfe-cta--BorderColor:               var(--pfe-theme--color--ui-complement, #464646);
  --pfe-cta__inner--BorderColor:        transparent;
  --pfe-cta--Color:                     var(--pfe-theme--color--ui-complement--text, #fff);
  --pfe-cta--BackgroundColor--hover:    var(--pfe-theme--color--ui-complement--hover, #131313);
  --pfe-cta--BorderColor--hover:        var(--pfe-theme--color--ui-complement--hover, #131313);
  --pfe-cta--Color--hover:              var(--pfe-theme--color--ui-complement--text, #fff);
  --pfe-cta--BackgroundColor--focus:    var(--pfe-theme--color--ui-complement, #464646);
  --pfe-cta__inner--BorderColor--focus: var(--pfe-theme--color--ui-complement--text, #fff);
  --pfe-cta--BorderColor--focus:        var(--pfe-theme--color--surface--darker, #464646);
  --pfe-cta--Color--focus:              var(--pfe-theme--color--ui-complement--text, #fff);
}

:host([pfe-priority="secondary"][pfe-color="complement"]) {
  --pfe-cta--BackgroundColor:           transparent;
  --pfe-cta--BorderColor:               var(--pfe-theme--color--ui-complement, #464646);
  --pfe-cta__inner--BorderColor:        transparent;
  --pfe-cta--Color:                     var(--pfe-theme--color--ui-complement, #464646);
  --pfe-cta--BackgroundColor--hover:    var(--pfe-theme--color--ui-complement, #464646);
  --pfe-cta--BorderColor--hover:        var(--pfe-theme--color--ui-complement, #464646);
  --pfe-cta--Color--hover:              var(--pfe-theme--color--ui-complement--text, #fff);
  --pfe-cta--BackgroundColor--focus:    var(--pfe-theme--color--surface--lighter, #ececec);
  --pfe-cta__inner--BorderColor--focus: var(--pfe-theme--color--ui-complement, #464646);
  --pfe-cta--BorderColor--focus:        var(--pfe-theme--color--ui-complement, #464646);
  --pfe-cta--Color--focus:              var(--pfe-theme--color--ui-complement, #464646);
}

:host([pfe-priority="secondary"][pfe-color="complement"].focus-within) {
  --pfe-cta--Color--hover:            var(--pfe-theme--color--ui-complement, #464646);
}

:host([pfe-priority="secondary"][pfe-variant="wind"]) {
  --pfe-cta--BackgroundColor:           transparent;
  --pfe-cta--BorderColor:               var(--pfe-theme--color--surface--border, #d2d2d2);
  --pfe-cta--Color:                     var(--pfe-broadcasted--color--ui-link, var(--pfe-theme--color--ui-link, #06c));
  --pfe-cta--FontWeight:                var(--pfe-theme--font-weight--normal, 500);
  --pfe-cta--BackgroundColor--hover:    var(--pfe-theme--color--surface--border--lightest, #ececec);
  --pfe-cta--BorderColor--hover:        var(--pfe-theme--color--surface--border, #d2d2d2);
  --pfe-cta--Color--hover:              var(--pfe-theme--color--ui-link--hover, #003366);
  --pfe-cta--Color--focus:               var(--pfe-theme--color--ui-link--hover, #003366);
  --pfe-cta--BorderColor--focus:         var(--pfe-theme--color--surface--border, #d2d2d2);
  --pfe-cta__inner--BorderColor--focus:  var(--pfe-cta--BorderColor--focus);
  --pfe-cta--TextDecoration--hover:      var(--pfe-theme--link--text-decoration--hover, underline);
}
